<template>
  <div class="game-details">
    <div v-if="game" class="game-details__container">
      <div class="game-details__header">
        <div class="game-details__title">
          <h1>Game Details</h1>
          <div class="game-details__decoration">
            <div class="decoration-dot"></div>
            <div class="decoration-line"></div>
            <div class="decoration-dot"></div>
          </div>
        </div>
        <div class="game-details__score-container">
          <div class="game-details__score-label">Final Score</div>
          <div class="game-details__score-wrapper">
            <div class="game-details__score-bg"></div>
            <div class="game-details__score">
              {{ game.score }}
              <img src="@/assets/coin.png" alt="Coin" class="coin-image">
            </div>
          </div>
        </div>
      </div>

      <div class="game-details__game-id">
        <div class="game-id-container">
          <span class="game-id-label">Game ID</span>
          <div class="game-id-value">
            {{ game.gameId }}
          </div>
        </div>
      </div>

      <div class="game-details__info">
        <div class="game-details__info-item">
          <span class="label">
            <div class="label-icon">
              <i class="fas fa-user"></i>
            </div>
            Player
          </span>
          <router-link :to="`/player/${game.userTag}`" class="value player-link">
            {{ game.userTag }}
          </router-link>
        </div>
        <div class="game-details__info-item">
          <span class="label">
            <div class="label-icon">
              <i class="fas fa-clock"></i>
            </div>
            Played At
          </span>
          <span class="value">{{ formatDate(game.timestamp) }}</span>
        </div>
      </div>

      <div class="game-details__actions">
        <button class="action-button play" @click="startNewGame">
          <div class="button-content">
          <i class="fas fa-play"></i>
            <span>Play New Game</span>
          </div>
        </button>
        <button class="action-button share" @click="shareGame">
          <div class="button-content">
            <i class="fas fa-share-alt"></i>
            <span>Share</span>
          </div>
        </button>
      </div>
    </div>

    <div v-if="game" class="bitcat-container">
      <img src="@/assets/bitcat-animation.webp" alt="BitCat" class="bitcat-image">
      <div class="bitcat-caption">Happy BITCAT</div>
    </div>

    <div v-else-if="error" class="game-details__error">
      <p>{{ error }}</p>
      <router-link to="/" class="action-button">Return Home</router-link>
    </div>

    <div v-else class="game-details__loading">
      Loading game details...
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
  name: 'GameDetailsPage',
  
  data() {
    return {
      game: null,
      error: null
    };
  },

  methods: {
    // ...mapActions(['startGame']),

    async fetchGameDetails() {
      try {
        const { data } = await axios.get(`/games/${this.$route.params.gameId}`);
        this.game = data;
      } catch (error) {
        this.error = error.response?.data?.error || 'Failed to load game details';
      }
    },

    formatDate(timestamp) {
      return new Date(timestamp).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },

    async startNewGame() {
      this.$router.push('/solo');
    },

    shareGame() {
      const url = window.location.href;
      if (navigator.share) {
        navigator.share({
          title: 'Check out my Bitcoin Cat game!',
          text: `Player ${this.game.userTag} scored ${this.game.score}â‚¿!`,
          url: url
        }).catch(console.error);
      } else {
        navigator.clipboard.writeText(url)
          .then(() => alert('Game link copied to clipboard!'))
          .catch(console.error);
      }
    }
  },

  created() {
    this.fetchGameDetails();
  }
};
</script>

